import { supabase } from "@/lib/supabase";
import { redirect } from "next/navigation";
import DashboardClient from "@/components/dashboard/DashboardClient";

export const dynamic = "force-dynamic";

export default async function DashboardPage() {
  // 1. Check Auth
  const { data: { user } } = await supabase.auth.getUser();
  if (!user) redirect("/login");

  // 2. Fetch Store Details (including new columns)
  const { data: store } = await supabase
    .from("stores")
    .select("*")
    .eq("owner_id", user.id)
    .single();

  // If no store, go to onboarding
  if (!store) redirect("/onboarding");

  // 3. Fetch Products (Count & List)
  const { data: products } = await supabase
    .from("products")
    .select("*, categories(name)")
    .eq("store_id", store.id)
    .order("created_at", { ascending: false });

  // 4. Fetch Orders
  const { data: orders } = await supabase
    .from("orders")
    .select("*")
    .eq("store_id", store.id)
    .order("created_at", { ascending: false });

  // 5. Calculate Stats
  const totalRevenue = orders?.reduce((acc, order) => acc + (order.status === 'completed' ? order.total_amount : 0), 0) || 0;
  const productCount = products?.length || 0;

  return (
    <DashboardClient 
      store={store} 
      initialProducts={products || []} 
      initialOrders={orders || []}
      stats={{ revenue: totalRevenue, productCount, views: store.view_count || 0 }}
    />
  );
}